import React from 'react';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import TextareaAutosize from '@mui/material/TextareaAutosize';
import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';


const Contact = () => {
  return (





    <Paper className='contact-form-con'>

      <div className='contact-info'>
        <h1> Contact Info </h1>
        <p>  </p>
      </div>

      <form>
        <TextField id="outlined-basic" label="Naam" variant="outlined" className='input' />
        <TextField id="outlined-basic" label="e-mail" variant="outlined" className='input' />
        <TextField id="outlined-basic" label="Onderwerp" variant="outlined" className='input' />

        <TextField
          fullWidth
          //multiline
          label="Bericht"
          InputProps={{
            inputComponent: TextareaAutosize,
            rows: 3
          }}
          className='input input-ms'
        />

        <Button className='send-btn' variant="contained" endIcon={<SendIcon />}>
          Verstur
        </Button>



      </form>



    </Paper>



  )
}





export default Contact;
